// React
import React from 'react'

// MUI
import { Autocomplete, Dialog, DialogContent, DialogTitle, FormControl, InputLabel, MenuItem, Select, Stack, TextField } from '@mui/material'

// local storage
import useLocalStorage from '@rehooks/local-storage'


function SettingsDialog(props) {
    const {open, close} = props

    const [unitOfMeasure, setUnitofMeasure] = useLocalStorage("unitOfMeasure", "inches")
    const [audioDevice, setAudioDevice] = useLocalStorage("audioDevice", null)

    const [audioOptions, setAudioOptions] = React.useState([])

    React.useEffect(() => {
        async function load() {
            let ids = []
            let output = []
            let devices = await navigator.mediaDevices.enumerateDevices()
            for (let device of devices) {
                if (device.kind === "audioinput") {
                    if (device.deviceId === "default" || device.deviceId === "communications") {
                        continue
                    }
                    if (!ids.includes(device.groupId)) {
                        output.push(device)
                        ids.push(device.groupId)
                    }
                }
            }
            setAudioOptions(output)
        }
        load()
    }, [])

    return (
        <Dialog
            open={open}
            onClose={close}
            scroll="body"
            maxWidth="md"
            fullWidth
        >
            <DialogTitle align="center">
                Settings
            </DialogTitle>
            <DialogContent>
                <Stack
                    spacing={1}
                    sx={{marginTop: 1}}
                >
                    <FormControl
                        variant="outlined"
                        fullWidth
                    >
                        <InputLabel id="unit-label">Unit of Measure</InputLabel>
                        <Select
                            labelId="unit-label"
                            value={unitOfMeasure}
                            onChange={event => setUnitofMeasure(event.target.value)}
                            label="Unit of Measure"
                        >
                            <MenuItem value="inches">Inches</MenuItem>
                            <MenuItem value="m">Meters</MenuItem>
                            <MenuItem value="mm">Milimeters</MenuItem>
                        </Select>
                    </FormControl>
                    <Autocomplete
                        fullWidth
                        value={audioDevice}
                        onChange={(event, newValue) => setAudioDevice(newValue)}
                        options={audioOptions}
                        isOptionEqualToValue={option => option.deviceId}
                        getOptionLabel={option => option.label}
                        renderInput={params => <TextField {...params} label="Audio Input Device" />}
                    />
                </Stack>
            </DialogContent>
        </Dialog>
    )
}

export default SettingsDialog